import Image from "next/image";
import Link from "next/link";
import { getArticles } from "@/lib/getArticles";

export default async function PaginatedNews({ params }: { params: { page: string } }) {
  const currentPage = Number(params.page);
  const data = await getArticles(currentPage);
  const articles = data.data;
  const pagination = data.meta.pagination;

  return (
    <div className="max-w-7xl mx-auto py-16 px-6">
      <h1 className="text-4xl font-bold mb-10 text-center">
        News – Page {currentPage}
      </h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {articles.map((article: any) => {
          const img = `http://localhost:1337${article.Article_Img[0].url}`
          const author = article.Author || "Unknown";

          return (
            <div
              key={article.id}
              className="rounded-2xl overflow-hidden bg-white shadow-lg"
            >
              {img && (
                <Image
                  src={`${img}`}
                  alt="Article image"
                  width={400}
                  height={250}
                  className="w-full h-56 object-cover"
                  unoptimized
                />
              )}

              <div className="p-4">
                <h2 className="font-semibold text-xl">
                  {article.attributes.Title}
                </h2>
                <p className="text-gray-600 text-sm mb-3">By {author}</p>
                <p className="text-gray-700 line-clamp-3">
                  {article.attributes.Description}
                </p>
              </div>
            </div>
          );
        })}
      </div>

      {/* Pagination */}
      <div className="flex justify-center gap-6 mt-12">
        {currentPage > 1 && (
          <Link
            href={`/news/${currentPage - 1}`}
            className="px-6 py-3 bg-gray-800 text-white rounded-xl"
          >
            ← Previous
          </Link>
        )}

        {currentPage < pagination.pageCount && (
          <Link
            href={`/news/${currentPage + 1}`}
            className="px-6 py-3 bg-black text-white rounded-xl"
          >
            Next →
          </Link>
        )}
      </div>
    </div>
  );
}
